import CourseItem from 'components/course-item/course-item'
export default {
  name: 'course-column',
  props: {
    courses: {
      type: Array,
      defalut: ''
    }
  },
  data () {
    return {
      pour: '',
      audioUrl: '',
      pause: false
    }
  },
  computed: {},
  components: {
    CourseItem
  },
  watch: {},
  methods: {
    play (url) {
      this.audioUrl = url
      // console.log(this.$refs.audio)
      // this.$refs.audio.play()
      // document.querySelectorAll('audio')[0].play()
    },
    invertedOrder () {
      if (this.pour === '') {
        this.pour = ''
        this.courses.reverse()
      } else {
        this.pour = ''
        this.courses.reverse()
      }
    }
  },
  created () {},
  mounted () {
  }
}



